@import url('https://fonts.googleapis.com/css?family=Roboto+Slab');


body {
  background: #000;
  color: #111;
  font-family:"Josefin Sans",'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  font-family: 'Roboto Slab', cursive;
}

body * {
  -webkit-transition: all 0.4s ease-in-out;
  -o-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
}

h1, h2, h3, h4 {
  font-family: "Alfa Slab One", 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}


::-moz-selection {
  background: #9B9B9B; /* WebKit/Blink Browsers */
}


::selection {
  background: #9B9B9B; /* WebKit/Blink Browsers */
}
::-moz-selection {
  background: #9B9B9B; /* Gecko Browsers */
}


.container-custom {
  background: #fff;
  margin: 0 auto;
  width: 100%;
  max-width: 1200px;
}


.header-component nav {
 position: fixed;
 top:0;
 z-index: 98;
 background: #111;
 width: 100%;
 text-align: right;
 max-width: 1200px;
}

.header-component nav ul {
  width: 100%;
  padding: 0;
  margin: 0;

}

.header-component nav ul li {
  list-style-type: none;
  padding: 10px;
  display: inline-block;
}

.header-component nav ul li a {
  display: block;
  text-transform: uppercase;
  font-size: 1.5rem;
  color: #eee;
  line-height: 1.5rem;
}

.header-component nav ul li a:hover {
  color: #EBAA17;
  font-weight: bold;
  -webkit-transition-duration: .2s;
       -o-transition-duration: .2s;
          transition-duration: .2s;
}


.header-component header {
  background-image: url('./images/bg2.jpg');
  background-repeat:no-repeat;
  background-attachment: fixed;
  background-position: bottom center;
  background-size: cover;
  height: 520px;
  width: 100%;
  display: block;
  position: relative;

}

.header-component header h1{
  font-size: 5rem;
  position: absolute;
  bottom: -10px;
  color: #fff;
  left: 0;
  line-height: 6rem;
}

p.intro {
  margin-left: 50px;
  width: 50%;
}

section {
  padding: 30px 10px;
  margin: 0;
  font-size: 24px;
  text-align: left;
  word-spacing: -3px;

  width: 100%;

}

section.p {
  white-space: nowrap;
}

.sub-heading {
  font-size: 4rem;
}

.multi-section {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 60px;
  -ms-flex-align: middle;
      align-items: middle;
  text-align: justify;

}

.multi-section div {
  width: 100%;
  line-height: 100%;
  white-space: pre-line;

}

.multi-section p {
 font-size: 16px;
 word-spacing: 1px;
}

.multi-section div img {
  width: 100%;
  -webkit-box-shadow: 0px 0px 2px #111;
          box-shadow: 0px 0px 2px #111;
}


.cards-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-auto-columns: minmax(auto, 400px);

  grid-column-gap: 10px;
}


.ui.card {
  display: inline-block;
  width: 100%;
  padding: 0;
  margin: 0;
}

.content a.header {
  font-size: 20px;
  word-spacing: 1px;
}

footer {
  margin-top: 150px;
  padding: 30px 10px;
  border-top: 1px solid #111;
  text-align: center;
}



/* ------ hamburguer button --------- */
#nav-icon{
  width: 30px;
  height: 45px;
  top: 20px;
  position: fixed;
  left: 0px;
  z-index: 99;
  display: none;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  cursor: pointer;
}


#nav-icon span{
  display: block;
  position: absolute;
  height: 3px;
  width: 100%;
  background: #DA9D1B;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
      transform: rotate(0deg);
  -webkit-transition: .20s ease-in-out;
  -o-transition: .20s ease-in-out;
  transition: .20s ease-in-out;
}

#nav-icon span:nth-child(1) {
  top: 0px;
}

#nav-icon span:nth-child(2) {
  top: 9px;
}

#nav-icon span:nth-child(3) {
  top: 18px;
}


#nav-icon.open span:nth-child(1) {
  top: 9px;
  -webkit-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
      transform: rotate(135deg);
}

#nav-icon.open span:nth-child(2) {
  opacity: 0;
  left: -60px;
}

#nav-icon.open span:nth-child(3) {
  top: 9px;
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
      transform: rotate(-135deg);
}


.icon {
    display: inline-block;
    background-color: none;
    color: black; /* Blue background */
    text-shadow: 1px 0.5px 0px black;

    border: none; /* Remove borders */
     /* White text */
    margin-bottom: 5px;/* Some padding */


    width: 100%;
    position: relative;
    cursor: pointer; /* Mouse pointer on hover */
    font-size: 4em;
}

/* Darker background on mouse-over */

