@media (min-width:320px) and (max-width:960px) {

  .header-component header h1{
    padding-bottom: 10px;
    font-size: 56px;
    position: absolute;
    color: #fff;
    line-height: 3.5rem;
  }

  #nav-icon {
    margin-left: 30px;
    display: block;

  }

  .header-component nav ul {
    height: 60px;
    overflow: hidden;
  }

  .header-component nav ul.show {
    height: 300px;
  }

  .header-component nav ul li {
    display: block;
    border-bottom:1px solid #F2AA21;
  }


  .header-component nav ul li a {
    margin-right: 10px;
    display: block;
    padding: 10px;
    font-size: 32px;

  }
  .header-component nav ul li a:hover {
    color: #F2AA21;
    opacity: 0.8;
  }

nav.li:active {
  display: none;
}

  .sub-heading {
    font-size: 36px;

  }

  section {
    width: 90%;
    margin: 0 auto;
    padding: 30px 10px;
    font-size: 22px;


  }

  p.intro {
      margin-left: 0px;
      width: 100%;
    }
  .cards-container {
    grid-template-columns: 1fr;
    grid-column-gap: 0;
    width: 80%;
    margin: 0 auto;

  }

  .multi-section {
    grid-template-columns: 1fr;
    grid-column-gap: 0px;
  }
  .multi-section div {
    margin-bottom: 50px;
    text-align: justify;
    text-justify: inter-word;

  }

  .ui.card {
    display: inline-block;
    width: 100%;
    padding: 0;
    margin: 0;
    margin-top: 10px;
  }
  ul.social {
    margin-left: 0px;
  }
}





@media (max-width:319px) {

.header-component header h1{
  padding-bottom: 10px;
  font-size: 64px;
  position: absolute;
  color: #fff;
  line-height: 56px;
}
#nav-icon {
  margin-left: 15px;
