}

.header-component nav ul li a {
  display: block;
  padding: 10px;
  font-size: 48px;
  line-height: 48px;

}
  .sub-heading {
    font-size: 32px;

  }

  section {
    width: 90%;
    margin: 0 auto;
    padding: 30px 0;
    font-size: 20px;
    text-align: left;
  }


  .cards-container {
    grid-template-columns: 1fr;
    grid-column-gap: 0;
    width: 80%;
    margin: 0 auto;

  }

  .multi-section {
    grid-template-columns: 1fr;
    grid-column-gap: 0;
  }
  .multi-section div {
    margin-bottom: 20px;
  }

  .ui.card {
    display: inline-block;
    width: 100%;
    padding: 0;
    margin: 0;
  }

}



